.header {
  height: 80px;
  background: #fff;
}
.header .header-top {
  height: 40px;
  text-align: right;
  line-height: 40px;
  border-bottom: 1px solid #1DA57A;
}
.header .header-top span {
  margin-right: 20px;
  cursor: pointer;
}
.header .header-top span:last-child {
  color: #1DA57A;
}
.header .header-bot {
  display: flex;
  align-items: center;
}
.header .header-bot > div {
  line-height: 40px;
}
.header .header-bot .header-bot-l {
  flex: 25% 1;
  text-align: center;
  font-size: 20px;
  position: relative;
}
.header .header-bot .header-bot-l:after {
  content: '';
  position: absolute;
  top: 100%;
  right: 50%;
  -webkit-transform: translateX(50%);
          transform: translateX(50%);
  border-top: 10px solid #fff;
  border-right: 10px solid transparent;
  border-bottom: 10px solid transparent;
  border-left: 10px solid transparent;
}
.header .header-bot .header-bot-r {
  flex: 75% 1;
  text-align: right;
}
.header .header-bot .header-bot-r .header-bot-r-sp1 {
  font-weight: 600;
}
.header .header-bot .header-bot-r img {
  width: 30px;
  height: 20px;
  margin: 0 20px;
}
.header .header-bot .header-bot-r .header-bot-r-sp2 {
  margin-right: 20px;
}
